import React from 'react';
import { Button } from '../../elements';
import { t } from 'i18next'
import styles from './Profile.module.css';
import bell from '../../assets/bell.png';

interface IProfile {
  data?: {
    address?: string;
  };
  onClick?: Function;
  [prop: string]: any;
}

function Profile({data = {}, onClick = () => {}}:IProfile) {
  console.log({data})
  return (
    <div className={styles.Profile}>
      <Button className={styles.notifications}>
        <img src={bell} alt={t("notifications")} />
      </Button>
      <Button title={data.address} onClick={() => onClick()}>
        {data.address?.replace(/^(.{7}).*(.{5})$/, '$1...$2')}
      </Button>
    </div>
  )
}

export default Profile;
