import React, { useState } from 'react';
import { t } from 'i18next'
import styles from './FilterAndSortMenu.module.css';
import { Button } from '../../elements';
import { ESort, EFilter } from '../../stores/AppStore';
import certified from '../../assets/certified.svg'

interface IFilterAndSortMenu {
  sort: ESort;
  filter: EFilter;
  onChange: (options: {sort: ESort, filter: EFilter}) => void;
}

function FilterAndSortMenu({
  sort, filter, onChange
}: IFilterAndSortMenu) {

  const [open, setOpen] = useState(false)
  
  return (
    <div className={styles.filterAndSort}>
      <Button onClick={() => setOpen(!open)}>{t("Filter & Sort")}</Button>
      { open && 
        <div className={styles.filterAndSortMenu}>
          <div className={styles.title}>{t("Sort by")}</div>
          <Button link="#" onClick={() => {
            onChange({sort: ESort.RECENTLY_ADDED, filter})
            setOpen(false)
          }}>
            {"Recently added"}
            {sort === ESort.RECENTLY_ADDED && <>&nbsp;<img src={certified} alt={t("selected")} /></>}
          </Button>
          <Button link="#" onClick={() => {
            onChange({sort: ESort.PRICE_LOW_TO_HIGH, filter})
            setOpen(false)
          }}>
            {"Price: Low to High "}
            {sort === ESort.PRICE_LOW_TO_HIGH && <>&nbsp;<img src={certified} alt={t("selected")} /></>}
          </Button>
          <Button link="#" onClick={() => {
            onChange({sort: ESort.PRICE_HIGH_TO_LOW, filter})
            setOpen(false)
          }}>
            {"Price: High to Low "}
            {sort === ESort.PRICE_HIGH_TO_LOW && <>&nbsp;<img src={certified} alt={t("selected")} /></>}
          </Button>
          <div className={styles.spacer}/>
          <div className={styles.title}>{t("Options")}</div>
          <Button link="#" onClick={() => {
            onChange({sort, filter: filter === EFilter.VERIFIED ? EFilter.NONE : EFilter.VERIFIED})
            setOpen(false)
          }}>
            {"Verified"}
            {filter === EFilter.VERIFIED && <>&nbsp;<img src={certified} alt={t("selected")} /></>}
          </Button>
        </div>
      }
    </div>
  )
}

export default FilterAndSortMenu;
