import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { t } from 'i18next'
import C from 'classnames'
import { useAppStore } from '../../stores/AppStore/AppStore';

import { Logo, Button, SocialButtons } from '../../elements';

import styles from './MainMenu.module.css';

import { Profile } from '..';
import { CloseIcon } from './CloseIcon'
import { MenuIcon } from './MenuIcon'

interface IMainMenu {
  vertical: boolean;
}

function MainMenu({vertical}: IMainMenu) {
  const [{
    profile = null,
  },,{
    /* signIn, */ signOut
  }] = useAppStore()

  const [menuOpened, setMenuOpened] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    setMenuOpened(vertical ? menuOpened : false)
  }, [vertical, menuOpened])

  const signInClick = async () => {
    console.log('signInClick')
    navigate('/signin');
    // await signIn()
  }
  
  const signOutClick = async () => {
    console.log('signOutClick')
    await signOut()
  }

  return (
    <nav className={vertical ? `${styles.MainMenu} ${styles.vertical}` : styles.MainMenu}>
      
      { 
        vertical ? (
          <div className={styles.groupItems}>
            <Logo large={true} onClick={() => navigate('/')}/>
            <div className={styles.spacer}/>
            <Button 
              className={
                C(styles.mainMenuButton, {
                  [styles.menuCloseButton]: menuOpened
                })
              } 
              onClick={() => setMenuOpened(!menuOpened)}
            >
              { menuOpened ? <CloseIcon /> : <MenuIcon /> }
            </Button>
          </div>
        ) : <Logo large={true} onClick={() => navigate('/')}/>
      }
      
      <div className={
        C(styles.MainMenu, {
          [`${styles.MainMenu} ${styles.vertical}`]: vertical,
          [styles.MainMenu]: !vertical,
          [styles.menuClosed]: vertical && !menuOpened
        })
      }>
      
        <Button link="/auctions" menu={vertical}>{t("Auction")}</Button>
        <Button link="#" menu={vertical} disabled title={t("Comming soon")}>{t("Events")}</Button>
        {profile && <Button link="#" menu={vertical}>{t("My Numiz")}</Button>}
        <div className={
          C(styles.spacer, {
            [styles.vertical]: vertical
          })
        }/>
        { vertical ? <SocialButtons /> : '' }
        <div className={styles.groupItems}>
          <Button wide primary className={styles.grow}>{t("Create Numi")}</Button>
          { 
            profile
            ? <Profile data={profile} onClick={() => signOutClick()}/>
            : <Button wide className={styles.grow} onClick={() => signInClick()}>{t("Sign in")}</Button>
          }
        </div>
      </div>

    </nav>
  )
}

export default MainMenu;
