import React from 'react';
import C from 'classnames'
import './Button.css';
import { Link } from 'react-router-dom';
import styles from './Button.module.css';

interface IButton {
  wide?: boolean;
  link?:string;
  primary?: boolean;
  bordered?: boolean;
  disabled?: boolean;
  menu?: boolean; 
  onClick?: Function;
  className?: string;
  title?: string;
  [prop: string]: any;
}

function Button({
  wide = false, 
  link = '',
  primary = false,
  className = '',
  children = null,
  bordered = false,
  menu = false,
  onClick = () => {},
  disabled = false,
  title = '',
  ...props
}: IButton) {
  return link ? (
    <Link {...{
      ...props,
      title,
      to: link,
      onClick: (e) => {
        console.log(e)
        onClick(e)
      },
      className: C(
        styles.Link, {
          className: className,
          [styles.wide]: wide,
          [styles.primary] : primary,
          [styles.menuItem] : menu ,
          [styles.disabled] : disabled,
        }
      ),
    }}>{ 
      menu 
      ? <><div>{ children }</div><div className={styles.menu}/></>
      : children 
    }</Link>
  ) : (
    <button {...{
      title,
      onClick: (e) => {
        console.log(e)
        onClick(e)
      }, 
      className: C(
        styles.Button, {
          className: className,
          [styles.wide]: wide,
          [styles.primary] : primary,
          [styles.menuItem] : menu ,
          [styles.disabled] : disabled,
          [styles.bordered] : bordered
        }
      ),
    }}>{ 
      menu 
      ? [<div>{ children }</div>, <div className={styles.menu}/>]
      : children
    }</button>
  )
}

export default Button;
