import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from "react-router-dom";
import './i18n';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import { AppStoreProvider } from './stores/AppStore';
import { createServer, Model } from "miragejs";

// TODO: Add mirage on/off control
// if(process.env.NODE_ENV === 'development') {
  
  createServer({
    models: {
      numiz: Model,
    },

    seeds(server) {
      server.db.createCollection('numiz')
      Array.from({length: 120}, (v, i) => i).map(n => (
        server.db.numiz.insert({
          id: n,
          name: 'numi mock' + n,
          // eslint-disable-next-line
          owner: Math.random() > Math.random() ? 'Jhon Dou' : 'mr. Smith',
          image: `https://picsum.photos/500?variator=${Math.random().toString().slice(3)}`,
          date: 1646708983182 + (~~(Math.random() * 100000000)),
          // eslint-disable-next-line
          atAuction: Math.random() > Math.random(),
          // eslint-disable-next-line
          verified: Math.random() > Math.random(),
          everPrice: (Math.random() * 10000).toFixed(2)
        })
      ))
    },

    routes() {

      function serverAuth(res: object) {
        console.log({res})
        let Profile = localStorage.getItem('Profile')
        if(Profile !== null) return JSON.parse(Profile);
        Profile = JSON.stringify(res)
        localStorage.setItem('Profile', Profile)
        return Profile
      }
    
      this.get("/auth/on", () => {
        return serverAuth({ success: true, address: '0:e26ebff8531f32de150074de9e7e83afa7aeeb2c8b16d0528d104758dfde31f2' })
      }, { timing: 500 })
      
      this.get("/auth/off", () => {
        return serverAuth({ success: false })
      }, { timing: 500 })
      
      this.get("/numiz/:sort/:filter/:page/", (schema, {params: {sort, filter, page}}) => {
        const numiz = filter === '1' 
          ? schema.db.numiz.filter(({verified}) => verified)
          : schema.db.numiz
        return parseInt(page) * 12 < numiz.length 
          ? JSON.stringify({
              success: true,
              numiz: numiz.sort((a, b) => {
                return sort === '0' 
                  ? (a.date - b.date) / Math.abs(a.date - b.date)
                  : sort === '1'
                    ? (a.everPrice - b.everPrice) / Math.abs(a.everPrice - b.everPrice)
                    : (b.everPrice - a.everPrice) / Math.abs(b.everPrice - a.everPrice)
              }).slice(parseInt(page) * 12, (parseInt(page) + 1) * 12)
            })
          : JSON.stringify({
              success: true,
              end: true
            })
      }, { timing: 500 })
    },
  }) 

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <AppStoreProvider>
        <App />
      </AppStoreProvider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

serviceWorkerRegistration.register();
reportWebVitals();
