import { TReducers, TScenarios, createDatanomy } from 'react-datanomy';
import { INumi } from '../../elements/Numi'

type TPage = {
  route: string;
  [property: string]: any;
};

type TError = typeof Error;

export enum ESort {
  RECENTLY_ADDED = '0',
  PRICE_LOW_TO_HIGH = '1',
  PRICE_HIGH_TO_LOW = '2'
}

export enum EFilter {
  NONE = '0',
  VERIFIED = '1'
}

interface IAppStore {
  errors: Array<TError>;
  profile: object | null;
  page: TPage;
  numiz: Array<INumi>;
  pageItems: number;
  numizLoading: boolean;
  allNumizLoaded: boolean;
  sortMode: ESort;
  filterMode: EFilter;
  [property: string]: any;
}

const AppStore: IAppStore = {
  errors: [],
  profile: null,
  page: {
    route: '/'
  },
  numiz: [],
  pageItems: 12,
  numizLoading: false,
  allNumizLoaded: false,
  sortMode: ESort.RECENTLY_ADDED,
  filterMode: EFilter.NONE,
}

const reducers: TReducers<IAppStore> = {
  error: (store, error) => ({ ...store, errors: store.errors.concat(error) }),
  logIn: (store, profile) => ({ ...store, profile }),
  logOut: (store) => ({ ...store, profile: null }),
  setPage: (store, page: TPage) => ({ ...store, page }),
  setNumiz: (store, numiz) => ({ ...store, numiz }),
  addNumiz: (store, numiz) => ({ ...store, numiz: store.numiz.concat(numiz) }),
  setNumizLoading: (store, numizLoading: boolean) => ({ ...store, numizLoading }),
  setAllNumizLoaded: (store, allNumizLoaded: boolean) => ({ ...store, allNumizLoaded }),
  setSortMode: (store, sortMode: ESort) => ({ ...store, sortMode }),
  setFilterMode: (store, filterMode: EFilter) => ({ ...store, filterMode }),
}

const scenarios: TScenarios<IAppStore> = {
  signIn: async (getContext: Function, user: object) => {
    const [{profile = null}, {logIn, error}] = getContext()
    if(!profile) {
      const res = await fetch('/auth/on').catch((err: Error) => error(new Error(err.message)))
      const data = await res.json().catch((err: Error) => error(new Error(err.message)))
      if(data.success) logIn(data); else error(new Error('Net: Authentication failed'))
      console.log('signed in:', user)
    }
  },
  
  signOut: async (getContext: Function, user: object) => {
    const [{profile}, {logOut, error}] = getContext()
    if(profile) {
      const res = await fetch('/auth/off').catch((err: Error) => error(new Error(err.message)))
      const data = await res.json().catch((err: Error) => error(new Error(err.message)))
      if(data.success) logOut(data); else error(new Error('Net: Sign out failed'))
      console.log('signed out:', user)
    }
  },
  
  loadNumiz:async (getContext: Function) => {
    const [{
      numiz, pageItems, numizLoading, sortMode, filterMode
    }, {
      addNumiz, setNumizLoading, setAllNumizLoaded, error
    }] = getContext()
    if (numizLoading) return;
    setNumizLoading(true)
    const res = await fetch(
      `/numiz/${sortMode.toString()}/${filterMode.toString()}/${numiz.length / pageItems}`
    ).catch(
      (err: Error) => error(new Error(err.message))
    )
    const data = await res.json().catch(
      (err: Error) => error(new Error(err.message))
    )
    if(data.success) {
      if(!data.end) addNumiz(data.numiz); else setAllNumizLoaded(true)
    } else error(new Error('Net: Numiz loading failed'))
    setNumizLoading(false)
  }
}

export const [
  AppStoreProvider,
  useAppStore,
  AppStoreContext
] = createDatanomy<IAppStore>(AppStore, reducers, scenarios)
