import React, { useEffect, useRef, useCallback } from 'react';
import { useAppStore } from '../../stores/AppStore/AppStore';
import { Button, Numi, FilterAndSortMenu } from '../../elements';
import { t } from 'i18next'
import styles from './HomePage.module.css';
import loader from '../../assets/Loader.svg';

function HomePage() {
  const [{
    numiz, numizLoading, allNumizLoaded, sortMode, filterMode
  }, {
    setSortMode, setFilterMode
  }, {
    loadNumiz
  }] = useAppStore()
  
  const loadingTrigger = useRef(null);

  const handleObserver = useCallback((entries: IntersectionObserverEntry[]) => {
    if (entries[0] && entries[0].isIntersecting) loadNumiz()
  }, [loadNumiz])

  useEffect(() => {
    if(numizLoading) return;
    var options = {
       root: null,
       rootMargin: "20px",
       threshold: 1.0
    };
    const observer = new IntersectionObserver(handleObserver, options);
    if (loadingTrigger.current) observer.observe(loadingTrigger.current)
  }, [handleObserver, numizLoading]);

  useEffect(() => { loadNumiz() }, [loadNumiz])

  return (
    <div className={styles.HomePage}>
      <div className={styles.banner}>
        <h1>{t("Auction banner")}</h1>
        <Button bordered>{t("Whats it all about?")}</Button>
      </div>
      <div className={styles.NumizListMenu}>
        <h2>{t("Latest Numiz")}</h2>
        <FilterAndSortMenu sort={sortMode} filter={filterMode} onChange={
          ({sort, filter}) => {
            console.log({sort, filter})
            if(sortMode !== sort) setSortMode(sort)
            if(filterMode!== filter) setFilterMode(filter)
          }
        }/>
      </div>
      <div className={styles.NumizList}>
        { numiz.map((data) => <Numi key={data.id} data={data}/>) }
      </div>
      { numizLoading 
        ? <div className={styles.loader}><img src={loader} alt={t("loader")} /></div>
        : !allNumizLoaded && <span className={styles.loadingTrigger} ref={loadingTrigger}>&nbsp;</span>
      }
    </div>
  )
}

export default HomePage;
