import React from 'react';
import QRCode from 'qrcode.react';
import { t } from 'i18next'
import { useMediaQuery } from 'react-responsive';
import C from 'classnames'
import { Button } from '../../elements'
import styles from './SignInPage.module.css';
import logo from '../../assets/Logo-compact.svg'
import art from '../../assets/art2.svg'

const surfURL = "https://ever.surf/"

declare global {
  interface Window {
    opera: string;
  }
}

const userAgent = navigator.userAgent || navigator.vendor || window.opera
const isAndroid = /android/i.test(userAgent)
const isIos = /iPad|iPhone|iPod/.test(userAgent)
const isMobyle = isAndroid || isIos

function SignInPage() {
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })
  return (
    <div className={styles.SignInPage}>
      <div className={styles.content}>
        <h1>{t("SignIn head")}</h1>
        <p>{isMobyle ? t("SignIn via TONSurf") : t("SignIn scan QR")}</p>
        { !isTabletOrMobile && [
          isMobyle && <Button primary>{t("Go to TONSurf App")}</Button>,
          <Button 
            primary={!isMobyle}
            onClick={() => window.open(surfURL, '_blank')}
            className={
              C({ [styles.golden]: isMobyle })
            }>{t("Get TONSurf App")}</Button>
        ]}
      </div>
      { isMobyle ? (
        <div className={styles.image}>
          <img src={art} alt="art" />
        </div>
      ):(
        <div className={styles.QRCode}>
          <QRCode 
            value={
              JSON.stringify({
                sessionId: Math.random().toString(), 
                callbackURL: 'https://numizteam.github.io', 
                info: "some numiz metadata", 
                signature: Array.from({length: 256}, (v,i) => (~~(Math.random() * 32 + 16)).toString(16)).join('')
              })
            }
            size={364}
            renderAs='svg'
            imageSettings={{ src: logo, width: 48, height: 48 }}
            />
        </div>
      )}
      { isTabletOrMobile && [
        isMobyle && <Button primary className={styles.fullWidth}>{t("Go to TONSurf App")}</Button>,
        <Button 
          primary={!isMobyle} 
          onClick={() => window.open(surfURL, '_blank')}
          className={
            C(styles.fullWidth, {
              [styles.golden]: isMobyle
            })
          }>{t("Get TONSurf App")}
        </Button>
      ]}
    </div>
  )
}

export default SignInPage;
