import React from 'react';
import styles from './Logo.module.css';

interface ILogo {
  large?: boolean;
  onClick?: Function;
}

function Logo({large = false, onClick}: ILogo) {
  return (
    <div 
      className={large ? `${styles.Logo} ${styles.Logotype}` : styles.Logo} 
      onClick={(e) => onClick && onClick(e)}
    />
  )
}

export default Logo;
